  font-family: 'Inter', sans-serif !important;
}

option {
  color: black;
}

.text-error {
  font-size: small;
  margin-top: 5px;
  color: red;
}

.toast-position {
  top: 20rem !important;
}

.Toastify__toast-container--top-right {
  top: em !important;
}

input.largerCheckbox {
  width: 40px;
  height: 40px;
}

::-webkit-scrollbar {
  width: 7px;
  height: 7px;
}

/* Track */
::-webkit-scrollbar-track {
  background-color: #fafafa;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background-image: linear-gradient(-45deg, #1900ff1e, #1b1b1b28);
  border-radius: 50px;
}

.animation-icon {
  margin-right: 4px;
}

.Toastify__toast--error {
  color: #EB5757;
  border-radius: 4px !important;
  background: white !important;
}

.Toastify__toast--error::after {
  position: absolute;
  color: #333333;
  font-size: 15px;
  left: 265px;
  padding-top: 14px !important;
}

.Toastify__toast--error::before {
  position: relative;
  z-index: 100000;
  left: 12px;
  top: 6px;
}

.Toastify__toast--success {
  color: '#3A9EA5' !important;
  border-radius: 4px !important;
  background: white !important;
}

.Toastify__toast--success::before {
  position: relative;
  color: '#3A9EA5' !important;
  z-index: 100000;
  left: 12px;
  top: 6px;
}

.Toastify__toast--success::after {
  position: absolute;
  color: #333333;
  font-size: 15px;
  left: 265px;
  padding-top: 14px !important;
}

.Toastify__toast--warning {
  color: '#E78326' !important;
  border-radius: 4px !important;
  background: white !important;
}

.Toastify__toast--warning::before {
  position: relative;
  z-index: 100000;
  left: 12px;
  top: 6px;
}

.Toastify__toast--warning::after {
  position: absolute;
  color: #E78326;
  font-size: 15px;
  left: 265px;
  padding-top: 14px !important;
}

.Toastify__toast-body {
  color: #444C63;
  font-size: 18px;
  padding-left: 10px;
  line-height: 20px;
  padding: 0px;
  padding-top: 20px;
  margin-left: 20px !important;
  box-shadow: none;
}

.Toastify__toast>button>svg {
  display: none;
