.page_404 {
    min-width: 100%;
    min-height: 100%;
    position: fixed;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
}

.page_404 img {
    background-repeat: no-repeat;
}

.four_zero_four_bg {
