    width: 700px;
    background-position: center;
    
}

.contant_box_404 {
    text-align: center;
    margin-top: -70px;
}

.contant_box_404 h1 {
    font-size: 80px;
}

.four_zero_four_bg h3 {
    font-size: 80px;
}

.link_404 {
    color: #fff !important;
    padding: 10px 20px;
    background: #39ac31;
    border-radius: 5px;
    margin: 20px 0;
    display: inline-block;
}
